      display: block; } }

.button-burger-mobile__burger-icon {
  position: relative;
  margin-top: 8px;
  margin-bottom: 8px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }
  .button-burger-mobile__burger-icon, .button-burger-mobile__burger-icon::before, .button-burger-mobile__burger-icon::after {
    display: block;
    width: 30px;
    height: 2px;
    background-color: #2c39f2;
    outline: 1px solid transparent;
    -webkit-transition-property: background-color, -webkit-transform;
    -moz-transition-property: background-color, -moz-transform;
    -o-transition-property: background-color, -o-transform;
    transition-property: background-color, transform;
    -webkit-transition-duration: 0.3s;
    -moz-transition-duration: 0.3s;
    -o-transition-duration: 0.3s;
    transition-duration: 0.3s; }
  .button-burger-mobile__burger-icon::before, .button-burger-mobile__burger-icon::after {
    position: absolute;
    content: ""; }
  .button-burger-mobile__burger-icon::before {
    top: -8px; }
  .button-burger-mobile__burger-icon::after {
    top: 8px; }

.button-burger-mobile_active .button-burger-mobile__burger-icon {
  background-color: transparent; }

.button-burger-mobile_active .button-burger-mobile__burger-icon::before {
  -webkit-transform: translateY(8px) rotate(45deg);
  -moz-transform: translateY(8px) rotate(45deg);
  -ms-transform: translateY(8px) rotate(45deg);
  -o-transform: translateY(8px) rotate(45deg);
  transform: translateY(8px) rotate(45deg); }

.button-burger-mobile_active .button-burger-mobile__burger-icon::after {
  -webkit-transform: translateY(-8px) rotate(-45deg);
  -moz-transform: translateY(-8px) rotate(-45deg);
